import SuperButton from "../h4/common/c2-SuperButton/SuperButton";
import React, {useState} from "react";
import {RequestsApi} from "./requestsApi";

export const Request = () => {
    const [success, setSuccess] = useState(false)
    const [response, setResponse] = useState<string>()
    const sendResponse = () => {
        RequestsApi.post(success)
            .then(response => {
                setResponse(response.data.errorText)
                console.log(response)
            })
            .catch(error => {
                console.log({...error})
                const newError = error.response.data.errorText
                console.log(error.response ? error.response.data.errorText : error.message)
                setResponse(newError)
            })
    }

    return (
        <div>

            <SuperButton onClick={sendResponse}>Hola</SuperButton>
            <input type={'checkbox'} onChange={(e) => {
                setSuccess(e.currentTarget.checked)
            }}/>
            <div>{response}</div>
        </div>
    )
}