import {ActionType} from "../../h10/bll/loadingReducer";

const CHANGE_THEME = 'CHANGE-THEME'
const initState = {
    theme: "dark"
};

type initialStateType = typeof initState

export const themeReducer = (state: initialStateType = initState, action: ActionType): initialStateType => { // fix any
    switch (action.type) {
        case CHANGE_THEME: {
            let copyState = {...state, theme: action.theme}
            return copyState;
        }
        default:
            return state;
    }
};

export const changeThemeAC = (theme: string): ActionType => {
    return {type: CHANGE_THEME, theme}
}; // fix any